import { useState } from 'react';
import './App.css';

function App() {
  document.title='Calculator'
  const [Answer,SetAnswer]=useState('');
  const [Prev,setPrev]=useState('');
  var A = '(';
  var B = ')';


  const AddToAnswer =(e)=>{
      var Res = e.target.innerHTML;
      if(Res === 'Clear'){SetAnswer('');return;}
      else if(Res === 'AC'){SetAnswer(Prev);return;}
      else if(Res === '(-)'){Res = '-';}
      else if(Res === 'Back'){SetAnswer(prev=>prev.substring(0,prev.length-1));return;}
      else if(Res === '='){
        var Result = 0;
        try{
          Result = eval(Answer.replace('^','**').replace('π','*3.14159265359*'));
        }catch(err){
          alert("Syntax Error");
          Result=''
        }
        SetAnswer(Result);
        setPrev(Result);
         return;
      }
      console.log(Res);
      SetAnswer(Old=>Old+Res);
  }

  //3.14159265359
  return (
    <div className="Container">
           <h1 className='Header'>{(Answer === '')? 0 : Answer}</h1>
           <button onClick={AddToAnswer}>{A}</button>
           <button onClick={AddToAnswer}>{B}</button>
           <button onClick={AddToAnswer}>^</button>
           <button onClick={AddToAnswer}>+</button>
           <button onClick={AddToAnswer}>1</button>
           <button onClick={AddToAnswer}>2</button>
           <button onClick={AddToAnswer}>3</button>
           <button onClick={AddToAnswer}>-</button>
           <button onClick={AddToAnswer}>4</button>
           <button onClick={AddToAnswer}>5</button>
           <button onClick={AddToAnswer}>6</button>
           <button onClick={AddToAnswer}>/</button>
           <button onClick={AddToAnswer}>7</button>
           <button onClick={AddToAnswer}>8</button>
           <button onClick={AddToAnswer}>9</button>
           <button onClick={AddToAnswer}>*</button>
           <button onClick={AddToAnswer}>.</button>
           <button onClick={AddToAnswer}>0</button>
           <button onClick={AddToAnswer}>(-)</button>
           <button onClick={AddToAnswer}>=</button>
           <button onClick={AddToAnswer}>Clear</button>
           <button onClick={AddToAnswer}>Back</button>
           <button onClick={AddToAnswer}>AC</button>
           <button onClick={AddToAnswer}>π</button>
\    </div>
  );
}

export default App;
